import React from 'react'
import Select from 'react-select'

import styled from 'styled-components'

const colourOptions = [
  { value: '1', label: '1' },
  { value: '2', label: '2' },
  { value: '3', label: '3' },
  { value: '4', label: '4' },
  { value: '5', label: '5' },
  { value: '6', label: '6' },
  { value: '7', label: '7' },
  { value: '8', label: '8' },
  { value: '9', label: '9' },
  { value: '10', label: '10' },
  { value: '11', label: '11' },
  { value: '12', label: '12' },
]

class Timer extends React.Component {
  state = {
    hour: '8',
    minute: '00',
    am: true,
  }

  handleTime = (hour, minute, am) => {
    this.setState({ hour, minute, am })
  }
  render() {
    const { hour } = this.state

    return (
      <Select
        className="color"
        value={hour}
        onChange={this.handleTime}
        options={colourOptions}
      />
    )
  }
}
export default Timer
